import React from 'react'
import Link from 'gatsby-link'
import MainGallery from "../components/gallery/gallery";

class IndexPage extends React.Component {
    render () {
        return (
            <MainGallery list={PHOTO_SET}/>
        );
    }
}

const PHOTO_SET = [
    {
        src: '/static/desktop6.jpg',
        srcSet: [
            '/static/mobile6.jpg 320w',
            '/static/tablet6.jpg 640w',
            '/static/desktop6.jpg 960w'
        ],
        sizes:['(min-width: 320px) 320px, (min-width: 640px) 640px, 960px'],
        width: 3,
        height: 4
    },
    {
        src: '/static/desktop5.jpg',
        srcSet: [
            '/static/mobile5.jpg 320w',
            '/static/tablet5.jpg 640w',
            '/static/desktop5.jpg 960w'
        ],
        sizes:['(min-width: 320px) 320px, (min-width: 640px) 640px, 960px'],
        width: 3,
        height: 4
    }
];

export default IndexPage



